@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%2351bac9' fill-opacity='0.34' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
  height: 100vh;
}

.app {
  height: 100%;
  text-align: center;
  padding: 0 20px 20px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #3b3b3b;
  background-color: #ffffff;
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%2351bac9' fill-opacity='0.34' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
}

.title {
  font-size: 36px;
  font-weight: 700;
  padding: 30px;
  color: #505050;
  text-transform: uppercase;
}

.filter__input {
  margin-bottom: 30px;
}

.message {
  font-size: 27px;
  margin-top: 20px;
  text-align: center;
  font-weight: bold;
}

.message-button {
  width: 100px;
  height: 35px;
  font-size: 20px;
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 15px;
}
.comment {
    margin-top: 30px;
    padding: 20px;
    background-color: rgba(119, 221, 119, 0.5);
    list-style-type: none;
  }
.comment-list {
    list-style: none;
  }

  .post {
    margin-bottom: 6px;
    padding: 50px;
    border: 5px solid rgba(133, 133, 133, 0.5);

   
  }
  
  .post-title {
    font-size: 30px;
    margin-bottom: 50px;
    margin-top: 50px;
  }

  .post-list {
    max-width: 960px;
    margin: 0 auto;
 
  }
